<template>
  <div class="app">
    <AppHeader>
      <slot name="sidebarToggler"></slot>
      <b-link class="navbar-brand">
        <img
          src="@/assets/logo.svg"
          class="navbar-brand-full p-3 navbar-brand-logo"
          alt="Expense Reduction Management Logo"
        />
      </b-link>
      <slot name="sidebarTogglerDefault"></slot>
    </AppHeader>
    <div class="app-body">
      <slot name="sidebar"></slot>
      <main class="main">
        <slot name="breadcrumb"></slot>

        <div class="container-fluid">
          <router-view></router-view>
        </div>
      </main>
    </div>
    <Footer>
      <b-row class="footer-text"></b-row>
    </Footer>
  </div>
</template>

<script>
import { Header as AppHeader, Footer } from "@coreui/vue";
import i18n from "@/plugins/i18n";
export default {
  name: "DefaultContainer",
  components: {
    AppHeader,
    Footer,
  },
  data() {
    return {
      languages: [
        { caption: "English", state: false, language: "en", title: "ENGLISH" },
        { caption: "Japanese", state: false, language: "ja", title: "日本語" },
      ],
    };
  },
  computed: {
    btnStates() {
      return this.buttons.map((btn) => btn.state);
    },
    name() {
      return this.$route.name;
    },
    isAdmin() {
      return localStorage.getItem("role") === "ADMIN";
    },
  },
  created() {
    if (localStorage.getItem("locale")) {
      this.languages[
        this._.findIndex(this.languages, {
          language: localStorage.getItem("locale"),
        })
      ].state = true;
    } else {
      this.languages[1].state = true;
    }
  },
  methods: {
    changeLocale(locale) {
      this._.map(this.languages, (lang) => (lang.state = false));
      this.languages[
        this._.findIndex(this.languages, { language: locale })
      ].state = true;
      i18n.locale = locale;
      localStorage.setItem("locale", locale);
    },
  },
};
</script>
<style scoped lang="scss">
.flag-button {
  margin-right: 5px;
}
.app-body {
  background: #ffffff;
}

.btn-primary {
  color: #000000;
  background-color: #ffffff;
  border-color: #ac000c;
}
.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.show > .btn-primary.dropdown-toggle {
  color: #fff;
  background-color: #ac000c;
  border-color: #ac000c;
}
</style>
<style lang="scss">
.navbar-brand-logo {
  max-width: 100%;
  width: 100%;
}
.main .container-fluid {
  padding: 0 5%;
}
@media (min-width: 991px) {
  .app-header .navbar-brand {
    display: block !important;
  }
}
</style>
